	module.exports = {"data":{"post":{"html":"<p>Photo albums, next attempt. This time I'm going to try to do it myself: http://www.shcon.com.</p>\n<p>Looks like <a href=\"http://www.vfth.com\">Al</a> has been bitten by the hiatus bug. Hope it doesn't last too long. <i>It lasted about 2 hours!</i></p>\n<p>A sign of adulthood: I'm starting to worry more about the state of the world I'm giving to my son and less about the one I inherited from my parents.</p>\n<p>My mom got caught out-of-town in a tornado warning yesterday. As the radio advised anyone in a car or trailer to seek shelter immediately, she pulled up to nearest house with lights on. The family took them down to their basement, and even invited her to stay for coffee after the storm had passed.</p>","fields":{"title":"No news would have been good news","date":"22 Jul 2001","url":"https://papascott.de/2001-07-22-no-news-would-have-been-good-news/"}}},"pathContext":{"slug":"/2001-07-22-no-news-would-have-been-good-news/","prev":{"fields":{"title":"Drunk and disorderly","date":"20 Jul 2001","slug":"/2001-07-20-drunk-and-disorderly/"}},"next":{"fields":{"title":"Butt naked","date":"24 Jul 2001","slug":"/2001-07-24-butt-naked/"}}}}

